import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  props: {
    /**/
    maxSize: {
      type: Number,
      default: 4000 //kb

    }
  },
  components: {
    quillEditor: quillEditor
  },
  data: function data() {
    return {
      content: '',
      quillUpdateImg: false,
      // loadingfalse,
      editorOption: {
        placeholder: "",
        theme: "snow",
        // or 'bubble'
        modules: {
          toolbar: {
            container: toolbarOptions,
            // container: "#toolbar",
            handlers: {
              image: function image(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader input").click();
                } else {
                  this.quill.format("image", false);
                }
              } // link: function(value) {
              //   if (value) {
              //     var href = prompt('url');
              //     this.quill.format("link", href);
              //   } else {
              //     this.quill.format("link", false);
              //   }
              // },

            }
          }
        }
      },
      serverUrl: "/v1/blog/imgUpload",
      // 
      header: {// token: sessionStorage.token
      } // token

    };
  },
  methods: {
    init: function init(data) {
      this.content = data;
    },
    onEditorBlur: function onEditorBlur() {//
    },
    onEditorFocus: function onEditorFocus() {//
    },
    onEditorChange: function onEditorChange() {
      //
      this.$emit("input", this.content);
    },
    // 
    beforeUpload: function beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },
    uploadSuccess: function uploadSuccess(res, file) {
      // res
      // 
      console.log(file);
      var quill = this.$refs.myQuillEditor.quill; // 

      if (res.code == 200) {
        // 
        var length = quill.getSelection().index; //   res.url

        quill.insertEmbed(length, "image", res.url); // 

        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      } // loading


      this.quillUpdateImg = false;
    },
    // 
    uploadError: function uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    }
  }
};